.intro {
    display: flex;
    justify-content: space-between;
    @include sp {
        flex-wrap: wrap;
    }
    &__box {
        position: relative;
        width:calc(50% - 8px);
        border-radius: 4px;
        border: 1px solid $border-color;
        @include sp {
            width: 100%;
        }
    }
    &__video {
        @include sp {
            margin-bottom: 16px;
        }
      
        overflow: hidden    ;
        video {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__content {
        // background: rgb(241, 235, 235);
        padding: 16px;
        h4 {
            @include font-config(16px, 400);
            @include text-spacing();
            margin-bottom: 8px;
        }
        p {
            @include font-config(14px, 400);
            @include text-spacing();
            overflow: hidden;
            -webkit-line-clamp: 9;
            -webkit-box-orient: vertical;
            display: -webkit-box;
        }
        a {
            display: block;
            @include font-config(14px, 600);
            @include text-spacing();
            color: #484848;
            text-decoration: none;
            margin-top: 4px;
        }
    }
}

.intro__content {
    &__item {
        h4 {
            text-align: center;
            text-transform: uppercase;
            font-weight: 600;
        }
    }
    &__btn {
        // position: absolute;
        // right: 16px;
        // left: 16px;
        // bottom: 16px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 8px;
        @include sp {
            flex-wrap: wrap;
        }
        a {
            display: flex;
            align-items: center;

            @include sp {
                width: 100%;
                justify-content: center;
            }
            span {
                margin-left: 8px;
            }
        }
    }
    &__intro-skill {
        background:  #000;
        padding: 8px;
        color: #fff !important;
        border-radius: 4px;
    } 
    &__intro-cosentino {
        background: #ce1313;
        padding: 8px;
        border-radius: 4px;
        color: #fff !important;
    }
}

// show room 
.showroom {
    &__item {
        margin: 8px;
    }
    &__wrapper {
        width: 900px;
        margin: 0 auto;
        @include sp {
            width: 100%;
        }
    }
    &__body {
        text-align: center;
        h3 {
            padding: 16px 0;
            text-transform: uppercase;
        }
    }
}

// 
.tab-latest-content {
    display: none;
    &.current {
        display: block;
    }
}
.tabs-latest {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 16px;
    li {
        list-style: none;
        padding: 8px;
        // border: 1px solid $border-color;
        border-radius: 999px;
        font-weight: 500;
        line-height: 150%;
        letter-spacing: 0.01em;
        color: #101010;
        text-transform: uppercase;
        font-size: 14px;
        cursor: pointer;
        &.current {
            border: 3px solid #000;
          
        }
    }
}
.releases {
    &__box {
        display: flex;
        justify-content: space-between;
        gap: 16px;
        margin-top: 26px;
        img {
            height: 100%;
            object-fit: cover;
        }
    }
}